<template>
  <div class="chatRoom">
    <p>欢迎{{username}}进入聊天室</p>
    <ol ref="chatContent">
      <li></li>
    </ol>

    <el-input type="text" placeholder="输入用户名" v-model="text"></el-input>
    <el-input type="button" value="提交" @click.native="handleEit"></el-input>
  </div>
</template>
<script>
export default {
  name: 'ChatRoom',
  data() {
    return {
      username: '写死的用户',
      text: ''
    }
  },
  methods: {
    handleEmit: function() {
      // 步骤二：客户端触发服务端的io
      var socket = io();
      socket.emit("liaotian",{
        "neirong" : this.text,
        "ren" : this.username
      });
    }
  },
  mounted() {
    // 步骤二：客户端也绑定聊天事件
    socket.on("liaotian",function(msg){
      this.$refs.chatContent.insertBefore("<li><b>" + msg.ren + "：</b>"+ msg.neirong + "</li>", null)
		});
  },
}
</script>
<style>
.chatRoom input {
  display: inline-block;
  width: 200px;
  margin:10px auto;
}
</style>
