<template>
  <div class="ChatIndex">
    <el-input type="text" placeholder="输入用户名" v-model="username"></el-input>
    <el-input type="button" value="提交" @click.native="handleSubmit(username)"></el-input>
  </div>
</template>

<script>
export default {
  name: 'ChatIndex',
  data() {
    return {
      username: ''
    }
  },
  methods: {
    handleSubmit: function() {
      let _this = this
      this.$axios.post('http://localhost:3000/singin?username=' + this.username)
      .then(function(res) {
        if (res.data == 'success') {
          _this.$router.push('./chartroom')
        }
      })
    }
  }
}
</script>

<style>
.ChatIndex input {
  display: block;
  width: 200px;
  margin:10px auto;
}
</style>
